import { Component, Input } from '@angular/core';
import { IManagedObject, InventoryService, IResultList } from '@c8y/client';

@Component({
  selector: 'c8y-operation-summary',
  templateUrl: 'operation-summary.component.html'
})
export class OperationSummaryComponent {
  @Input()
  name: string;

  @Input()
  description: string;

  @Input()
  set deviceQueryString(query: string) {
    this.displayDeviceCount = true;
    const filters = {
      q: query || '',
      pageSize: 1,
      currentPage: 1,
      withTotalPages: true
    };
    this.inventoryService
      .list(filters)
      .then((result: IResultList<IManagedObject>) => (this.deviceCount = result.paging.totalPages));
  }

  deviceCount = 0;
  displayDeviceCount = false;

  constructor(private inventoryService: InventoryService) {}
}
